import React from 'react';
import Select from "./Select";

export default function SelectRange ({ first, last, ...props }) {

    const opts = [];
    const inc = first<last ? 1 : -1;
    for (let n = first;;n+=inc){
        const v = String(n);
        opts.push( <option key={v} value={v}>{v}</option> );
        if (n===last) break;
    }

    return <Select {...props} optionElements={opts} />;
}

SelectRange.defaultValidator = Select.defaultValidator;



