import React from 'react';
import PropTypes from 'prop-types';
import { translate } from '../Translator';

export default class CheckBox extends React.Component {

    static propTypes = {
        ariaDescribedBy: PropTypes.string,
        id: PropTypes.string,
        invalid: PropTypes.bool,
        label: PropTypes.string,
        onBlur: PropTypes.func,
        onChange: PropTypes.func,
        onFocus: PropTypes.func,
        onStateChange: PropTypes.func,
        required: PropTypes.bool,
        value: PropTypes.bool
    };

    onBlur = () => {
        // if (!this.props.touch) this.props.onStateChange( { touch: true } );
    };

    onChange = (ev) => {
        console.log('CB',ev.target.checked);
        this.props.onStateChange( { value: !!ev.target.checked } ); // ev.target.checked ? 'Y' : 'N'
    };

    render() {
        const { id, value, label, required, onFocus } = this.props;
        const labelText = translate(label || id);
        return (
            <span className="checkbox">
                <input
                    checked={!!value}
                    id={id}
                    onBlur={this.onBlur}
                    onChange={this.onChange}
                    onFocus={onFocus}
                    required={required}
                    type="checkbox"
                />
                <label htmlFor={id}>{labelText}</label>
            </span>
        );
    }

}



