import React, {Component} from 'react';
import logo from './logo.svg';
import './App.css';
import FormField from './FormField';
import SelectCountry from './SelectCountry';
import { makeMade } from './ConnectedElement';
import AddressSection from './AddressSection';
// import { doLogin } from './AirBNB';
import { setLanguage } from './Translator';
import { redrawDefaults } from './ConnectedElement';

import BippoForm from './hack/BippoForm';
import PageLink from "./hack/PageLink";
import ReadItOut from "./components/ReadItOut";

const OverseasAddress = AddressSection.makeAddress('overseasAddress');

export default class HomePage extends Component {

    LangSelect = makeMade('nationality', SelectCountry, this );

    static getStateFields (/* clues */) {  // for validation and sanitization
        return [
            'myTestField',
            'inputAmount'
        ];
    }

    componentDidMount () {
        console.log('componentDidMount HomePage');
    }

    somethingChanged = (disp,id,newData,oldData) => {
        console.log('APP somethingChanged',id,newData);
        if (id==='nationality') {
            redrawDefaults();
            setLanguage( newData.value );
            this.setState({ lang: newData.value });
            disp({ type: 'BOOST', value: newData.value });
        }
    };

    testLogin = () => {
        // doLogin();
    };

    render() {
        const Lang = this.LangSelect;
        return (
            <div className="App">
                <header className="App-header">
                    <img src={logo} className="App-logo" alt="logo"/>
                    <h1 className="App-title">Welcome to React</h1>
                    <ReadItOut>Welcome to this react page</ReadItOut>
                </header>
                <p className="App-intro">
                    This is a test form with React
                </p>
                <div className="form">
                    <FormField id="myTestField"/>
                    <FormField id="inputAmount"/>
                    <FormField id="totalAmount"/>
                    <FormField id="somethingElse"/>
                    <FormField id="country" InputComponent={SelectCountry}/>
                    <FormField id="otherCountry" show={false}/>
                    <AddressSection id="homeAddress" />
                    <OverseasAddress/>

                    <BippoForm id="Bippo" path={['myForm','sectionA']}/>
                    <BippoForm id="Zippo" path={['myForm','sectionZ']}/>
                    <div style={{ maxWidth: '300px', marginTop: '32px' }}>
                        <Lang/>
                    </div>
                </div>
                <p>
                    <button type="button" onClick={this.testLogin}>Confirmo</button>
                    <span> Try click </span>
                    <span>here </span>
                    <button type="button" onClick={this.testLogin}>Validate</button>
                    <PageLink url="/zooter/2" ttl="Go to second page"/>
                    <PageLink url="/zooter/3" ttl="Third page"/>
                    <PageLink url="/zooter/4" ttl="Fourth page"/>
                </p>
                <p>...</p>
            </div>
        );
    }
}



