import React from "react";
import {connect} from "react-redux";
// here
import FormSection from "./FormSection";
import Input from "../components/Input";
import InputDate from "../components/InputDate";
import Select from "../components/Select";
import SelectRange from "../components/SelectRange";
import CheckBox from "../components/CheckBox";
//import RadiosFunc from "../components/RadiosFunc";
import {lookup} from '../startRedux';
import {setLanguage, translate} from "../Translator";

import { makeNoboField, makeChildField, FormFieldRender, updateRedux } from "./NoboField";

const required = true;

const FishType = makeNoboField('fishType',Select,{ options: 'country' });
const DogType = makeNoboField('dogType',Input,{ maxLength: 6, required });
const EggType = makeNoboField('eggType',Input,{ maxLength: 80 });
const CatType = makeNoboField('catType',Input,{ maxLength: 80, validator: validateMoreThan });
const PurchaseDate = makeNoboField('purchaseDate',InputDate,{ required: true });

const priceProps = { id: 'fud', compo: Input,  minLength: 4, maxLength: 7, numeric: true, type: 'number', step: 1000, min: 0, max: 100000 };

export class NoboTest extends React.PureComponent {

    getId () {
        return this.props.id;
    }

    doReset = () => {
        this.props.updateRedux({ type: 'SET', key: this.props.id, value: {} });
    };

    // UGLY - BUT WORKS

    renderA () {
        const m = (id) => { return { id, sectionId: this.props.id, compo: Input }; };
        return (
            <div>
                <FormFieldRender {...m('foo')} />
                <FormFieldRender {...m('bar')} />
                <FormFieldRender {...m('baz')} compo={InputDate} useMonthSelect />
                <FormFieldRender {...m('quo')} />
            </div>
        );
    }

    // RUBBISH - draws each field every time !!!!

    Field = (props) => <FormFieldRender sectionId={this.props.id} compo={Input} {...props} />;

    renderB () {  // draws each field every time !!!!
        const { Field } = this;
        return (
            <div>
                <Field id="zoo" />
                <Field id="baz" />
                <Field id="bar" compo={InputDate} useMonthSelect />
                <Field id="foo" />
            </div>
        );
    }

    // validation and data clean

    validateSection = () => {
        const { values } = this.props;
        const { ZokType } = this;
        console.log(values);
        const e1 = ZokType.defaultValidator( values.zokType ); console.log('e1',e1);
        const e2 = CatType.defaultValidator( values.catType, { other: values.eggType } );        console.log('e2',e2);
        const e3 = validateString(values[priceProps.id],priceProps); console.log('e3',e3);
        const e4 = PurchaseDate.defaultValidator( values.purchaseDate ); console.log('purchaseDate',e4);
        const e5 = InputDate.defaultValidator( values.zort, {} ); console.log('zort',e5);
    };

    // best way ????

    ZokType = makeChildField(this, { id: 'zokType', component: Select, options: 'country', required });
    RetireAge = makeChildField(this, { id: 'retireAge', component: SelectRange, first: 2030, last: 2018, required });

    render () {
        const { values, id } = this.props;
        const { fishType, eggType, fud } = values;
        const { ZokType, RetireAge } = this;
        return (
            <FormSection id={id} section={this}>
                <FishType/>
                {fishType==='OTH' && <DogType/>}
                <EggType foo={fishType}/>
                <CatType other={eggType}/>
                <ZokType foo={fishType}/>
                <RetireAge/>
                <PurchaseDate/>

                <FormFieldRender id={'fud'} sectionId={id} compo={Input} />
                <FormFieldRender sectionId={id} {...priceProps} />

                <p>Entered "{fud}" {parseFloat(fud)}</p>
                <FormFieldRender id={'zort'} sectionId={id} compo={InputDate} useMonthSelect foo={eggType} />
                <FormFieldRender id={'lang'} sectionId={'coreData'} compo={Select} options="country" onChange={changeLang}/>
                {id==='NT1' && <FormFieldRender id={'showAllErrors'} sectionId={'coreData'} compo={CheckBox} noLabel key={id+'/xxx'} />}
                <button className="btn btn-default" onClick={this.validateSection}>{translate('validate')}</button>
            </FormSection>
        );
    }
}

function validateMoreThan (value, props) {
    const other = props.other;
    const a = parseInt(value,10);
    const b = parseInt(other,10);
    if (a<=b) return 'This must be more than other';
}

function changeLang (ev) { setLanguage(ev.target.value) }

// CONNECT THIS FORM TO REDUX SO I CAN PICKUP STATE

function mapStateToProps (state,props) {
    const values = lookup([props.id],state.formState);
    return {
        values
    };
}

export default connect(mapStateToProps,{updateRedux})(NoboTest);

// SOME IDEAS

export const FormStateLikeThis = {
    FamilyDetailsForm: {
        values: {},
        props: {},
        errors: {}
    },
    VehicleDetailsForm: {},
    AddressDetailsForm: {}
};

export function getErrors (data,props) {
    return isRequired (data,props) ||
        isMinLength (data,props) ||   // no point calling these if no data
        isFormatted (data,props);
}

function isRequired () {}   // undef, or ['required']
function isMinLength () {}
function isFormatted () {}


export function validateString (input, props) {
    const value = input && input.trim();
    if (!value) {
        if (props.required)
            return 'isRequired';
        return;
    }
    const minLength = props.minLength;
    if (minLength && value.length<minLength)
        return { err: 'minLength', values: { minLength } };
    const maxLength = props.maxLength;
    if (maxLength && value.length>maxLength)
        return { err: 'maxLength', values: { maxLength } };
    // numeric
    // decimal
    if (props.validator)
        return props.validator(value,props);
}



