import React from 'react';
import makeField from './RedoConnecto';
import BippoField, { dateValidator } from './BippoField';
import {connect} from 'react-redux';
import {lookup} from '../startRedux';

import CheckBox from '../components/CheckBox';      // eslint-disable-line no-unused-vars
import Input from '../components/Input';            // eslint-disable-line no-unused-vars
import InputDate from '../components/InputDate';    // eslint-disable-line no-unused-vars
import Radios from '../components/Radios';          // eslint-disable-line no-unused-vars
import Select from '../components/Select';          // eslint-disable-line no-unused-vars
import RadiosFunc from '../components/RadiosFunc';          // eslint-disable-line no-unused-vars
import UltraSection from './UltraSection';          // eslint-disable-line no-unused-vars

// TODO make sure state is valid (ie 'Other' is shown only if Type 'Other' is selected)
// TODO fancy stuff - pick USA, show list of US states - pick Canada, list of provinces - other, free text
// TODO actions - user picks something, this resets another field
// TODO form level validation AND sanitization
// TODO active validation and limits - ie change minLength, maxLength & validation rules for one field based on another

function makeRadio(t,id,props) { return makeField(t,BippoField,id, { comp: RadiosFunc, showLabel: false, editProps: props } ); }
function makeCheckBox(t,id,props) { return makeField(t,BippoField,id, { comp: CheckBox, showLabel: false, ...props } ); }

// export const ShowErrorsContext = React.createContext(false);

class BippoForm extends React.Component {

    //constructor(props) {
    //    super(props);
    //}

    componentWillMount () {
        this.checkFieldState({});
    }

    //shouldComponentUpdate () {  // nextProps
    //    return false;
    //}

    checkFieldState (newState) {
        // needs to access state
    }

    vali = () => {
        const redux = this.props.getRedux();

        // HOW THE F DO WE GET AT THE CONTENT VALUE IN THE FIELDS ???
        const { Surname, Forename } = this;
        //Surname.validator ();
        console.log(this.props.stateData,'NAME IS',Surname.id, Surname.getValue(),Forename.getValue(), this.Gloop.getValue() );
        console.log('this',this);
        const ui = this.render();
        console.log('FORM',ui);
        const C1 = ui.props.children[0];
        console.log('C1',C1);
        // const Q = C1.type(); // Cannot call a class as a fnuction
        const Q = new C1.type( { store: { getState: () => redux } }, C1 ); // runs but not right methinks
        console.log('Q',Q);
        const BIP = Q.render();
        console.log('BIP',BIP);

        const FUD = new BIP.type(BIP.props);
        console.log('FUD',FUD.render());
        // console.log('WC',C1.type.WrappedComponent);
        /*
        const C7 = ui.props.children[7];
        console.log('C7',C7);
        const G = new C7.type();
        console.log('G',G.render());
        */

    };

    Surname = makeField(this,BippoField,'surname', { required: true } );
    Forename = makeField(this,BippoField,'forename');
    PostCode = makeField(this,BippoField,'postcode', { required: true, editProps: { maxLength: 10 } });
    DOB = makeField(this,BippoField,'dateOfBirth', { comp: InputDate, required: true, validator: dateValidator, editProps: { useMonthSelect: true } } );
    Gloop = makeField(this,BippoField,'gloop'); // , { hidden: true });
    // Rads = makeField(this,BippoField,'pickDoo', { comp: RadiosFunc, editProps: { label: 'Foobar', options: 'country' }, showLabel: false } );
    Rads = makeRadio(this,'pickDoo',{ label: 'Foobar', options: 'country' });
    Consent = makeField(this,BippoField,'consent', { comp: CheckBox, label: 'consentToEmail', showLabel: false } );
    // NoEmail = makeField(this,BippoField,'noemail', { comp: CheckBox, label: 'noEmail', showLabel: false } );
    NoEmail = makeCheckBox(this,'noemail', { label: 'noEmail' } );
    HoHum = makeCheckBox(this,'greatDeals');

    fieldChanged (disp,id,newData,stateData) {
        // console.log('FIELD CHANGED',id,newData);
        this.checkFieldState({}); // DOH needs to use new state
    }

    zoo () {
        this.renderTest();
        console.log( this.childForm );
        this.kazoom();
    }

    renderTest () {
        const { FormComponent } = this.props;
        return (
            <FormComponent
                form={this}
                ref={child => this.childForm = child}
            />
        );
    }

    kazoom () {
        const { TextField, DateField, CustomerType, Section } = this.props; // hacks
        return (
            <Section kind="doowat">
                <TextField id="surname" required minLength={3} />
                <DateField id="snpDate" mode="past" minYear={2010} />
                <CustomerType id="custType" optionsId={this.getCTId()} />
            </Section>
        );
    }

    render () {
        const { stateData = {} } = this.props;
        //const zoot = stateData && stateData.surname && parseInt(stateData.surname.value,10)>0;
        //const reverse = stateData && stateData.gloop && parseInt(stateData.gloop.value,10)>0;
        //
        const { surname, gloop, postcode } = stateData;
        const zoot = surname && parseInt(surname.value,10)>0;
        const reverse = zoot && gloop && !isNaN( parseInt(gloop.value,10) );
        // done
        //console.log('Render',stateData);

        const { Surname, Forename, PostCode, DOB, Rads, Consent, NoEmail, HoHum } = this;
        const pop = {}; // (postcode && postcode.value==='X') ? { showErrors: true } : { showErrors: false };
        const ShowErrors = (postcode && postcode.value==='X');

        if (reverse) {
            return (
                <UltraSection showErrors={ShowErrors}>
                    <this.Gloop key="glp" hidden={!zoot}/>
                    <Forename key="fnm"/>
                    <Surname key="snm"/>
                    <PostCode/>
                    <button onClick={this.vali}>Click</button>
                </UltraSection>
            );
        }
        return (
            <UltraSection showErrors={ShowErrors}>
                <Surname key="snm"/>
                <Forename key="fnm"/>
                <this.Gloop hidden={!zoot} key="glp"/>
                <PostCode {...pop}/>
                <DOB key="dob"/>
                <div>
                    <Rads  {...pop}/>
                    <Consent  {...pop}/>
                    <NoEmail/>
                    <HoHum/>
                </div>
                <button onClick={this.vali}>Click</button><TestThing doodle flap/>
            </UltraSection>
        );
    }

}

const DEF_DATA = {};

function mapStateToProps (state,props) {
    const stateData = lookup(props.path,state.formState,DEF_DATA);
    return {
        stateData // : state.formState[props.id] || DEF_DATA
    };
}

const getRedux = () => {
    return function (dispatch, getState) {
        return getState();
    };
};

export default connect( mapStateToProps, { getRedux } )(BippoForm);
//export default BippoForm;

class TestThing extends React.Component {
    render () {
        return <span> Some <b>damned</b> thing!</span>;
    }
}



