import React from 'react';
import { translate, getOptionSet } from "../Translator";

export default class Radios extends React.Component {

    renderRadios (id,value,name) {
        const set = getOptionSet(name);
        const opts = [];
        Object.keys(set).forEach( key => {
            const full = id+'-'+key;
            opts.push(
                <span key={full}>
                    <input id={full} name={id} type="radio" value={key} checked={key===value} onChange={this.onChange} />
                    <label htmlFor={full}>{set[key]}</label>
                </span>
            );
        });
        return opts;
    }

    onChange = (ev) => {
        this.props.onStateChange( { value: ev.target.value } );
    };

    render() {
        const { id, value, options, label } = this.props;
        const labelText = translate(label);
        return (
            <fieldset aria-label={labelText} className="radio-fieldset" id={id}>
                <legend>{labelText}</legend>
                {this.renderRadios(id,value,options)}
            </fieldset>
        );
    }
}



