import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import { translate } from './Translator';

class FormField extends Component {

    static propTypes = {
        id: PropTypes.string,
        InputComponent: PropTypes.func,
        myData: PropTypes.shape({})
    };

    setField (key, value) {
        this.props.updateReduxState({ type: 'SET', key, value });
    }

    showField (key, value) {
        this.props.updateReduxState({ type: 'SHOW', key, value });
    }

    debug = () => {
        console.log(this.props.getReduxState().formState);
    };

    onBlur = () => {
        const { id, myData } = this.props;
        if (myData.touched) return;
        this.setField(id,{ ...myData, touched: true });
    };

    onChange = (ev) => {
        const { id, myData } = this.props;
        //console.log('FormField onchange',id,ev.target.value);
        const newData = {
            ...myData,
            value: ev.target.value
        };
        this.setField(id,newData);
        // HACK TEST
        if (id==='inputAmount') {
            this.setField('totalAmount',{ value: newData.value, touched: true });
        }
        if (id==='somethingElse') {
            this.setField('nationality',{ value: newData.value });
        }
        // SHOW HIDE
        if (id==='country') {
            /*
            const oth = this.props.getReduxState().formState.otherThing || {};
            const show = !!oth.show;
            const smod = !!newData.value;
            if (show!==smod)
                this.props.updateReduxState('otherThing',{ ...oth, show: smod });
            */
            this.showField('otherCountry', newData.value==='OTH');
        }
    };

    getInputComponent () {
        if (this.props.InputComponent)
            return this.props.InputComponent;
        if (this.props.compo==='select')
            return 'select';
        return 'input';
    }

    render() {
        const { id, myData } = this.props;
        const val = myData.value || '';
        const touched = myData.touched;
        const invalid = touched && !val;
        const Input = this.getInputComponent();
        const fub = { ...this.props, ...myData };
        if (fub.show===false)
            return null;
        const label = translate(id);

        return (
            <div className={invalid ? 'field invalid' : 'field'}>
                <label htmlFor={id} onClick={this.debug}>{label}</label>
                <Input type="text" id={id} onBlur={this.onBlur} onChange={this.onChange} value={val}/>
                {invalid && <span>Please enter {id}</span>}
            </div>
        );
    }
}

const DEF_DATA = {};

function mapStateToProps(state,props) {
    const dat = state.formState[props.id] || DEF_DATA;
    // console.log(props.id,dat);
    return {
        myData: dat
    };
}

const updateReduxState = (action) => {
    return function (dispatch/*, getState*/) {
        /*const state = getState();
        const form = state.globalState[formId] || {};
        const newState = fullReset===true ? { ...data } : { ...form, ...data };
        const action = { type: 'UPDATE_GLOBAL_STATE', data: { [formId]: newState } };*/
        // const action = { type: 'SET', key: formId, value: data };
        return dispatch(action);
    };
};

const getReduxState = () => {
    return function (dispatch, getState) {
        return getState();
    };
};

export const mapDispatchToProps = {
    updateReduxState,
    getReduxState
};

export default connect(mapStateToProps,mapDispatchToProps)(FormField);



