import React from 'react';

export default class InputText extends React.Component {

    static validateInput (props) {
        if (!props.value) {
            if (props.editProps && props.editProps.optional)
                return null;
            return props.id + '-error';
        }
        if (props.editProps && props.editProps.minLength) {
            if (props.value.length<props.editProps.minLength)
                return props.id + '-minlength';
        }
    }

    render () {
        const { id, onBlur, onChange, onFocus, value = '', maxLength } = this.props;
        return (
            <input
                autoCorrect="off"
                autoComplete="off"
                className="input"
                type="text"
                id={id}
                onBlur={onBlur}
                onChange={onChange}
                onFocus={onFocus}
                spellCheck="false"
                value={value}
                maxLength={maxLength}
            />
        );
    }

}



