import React, {Component} from 'react';
import logo from './logo.svg';
import './App.css';
import SelectCountry from './SelectCountry';
import { makeMade } from './ConnectedElement';
import BippoForm from './hack/BippoForm';
import PageLink from "./hack/PageLink";
import ReadItOut from "./components/ReadItOut";

export default class SecondPage extends Component {

    componentDidMount () {
        console.log('componentDidMount SecondPage');
    }

    LangSelect = makeMade('nationality', SelectCountry, this );

    render() {
        const { LangSelect } = this;
        return (
            <div className="App">
                <header className="App-header">
                    <img src={logo} className="App-logo" alt="logo"/>
                    <h1 className="App-title">The Second Page</h1>
                    <ReadItOut>You are on page 2</ReadItOut>
                </header>

                <div className="form">
                <div style={{ maxWidth: '300px', marginTop: '32px' }}>
                    <LangSelect/>
                    <PageLink url="/zooter" ttl="First"/>
                    <PageLink url="/zooter/3" ttl="Next"/>
                    <PageLink url="/zooter/4" ttl="Last"/>
                </div>

                <BippoForm id="Bippo" path={['myForm','sectionA']}/>
                <BippoForm id="Zippo" path={['myForm','sectionZ']}/>
                <BippoForm id="Fippo" path={['myForm','sectionG']}/>
                </div>

                <footer>
                    Footer
                </footer>

                <div className="popup-blanker">...</div>
                <div className="spinner">...</div>
            </div>
        );
    }
}



