import React from 'react';

export const FormSectionContext = React.createContext({});

export default function FormSection ({ id, section, children }) {
    return (
        <FormSectionContext.Provider value={section}>
            <div id={id}>
                {children}
            </div>
        </FormSectionContext.Provider>
    );
}

