import React from 'react';
import PropTypes from 'prop-types';
import { translate } from '../Translator';
import { ShowErrorsContext } from './UltraSection';

export default class BippoField extends React.Component {

    static propTypes = {
        comp: PropTypes.func,
        editProps: PropTypes.shape({}),             // from JSX
        hidden: PropTypes.bool,                     // whole field is hidden if true
        id: PropTypes.string,                       // 'surname'
        path: PropTypes.arrayOf(PropTypes.string),  // ['testForm','relatives',0,'parents']
        parent: PropTypes.shape({}),                // ref to containing parent section
        required: PropTypes.bool,                   // passed to component - can be overriden in editProps
        showLabel: PropTypes.bool,                  // can be used to hide labels
        stateData: PropTypes.shape({}),             // value, touch
        updateRedux: PropTypes.func,
        validator: PropTypes.func,
        showErrors: PropTypes.bool
    };

    onChange = (ev) => {
        const {path, stateData, updateRedux, parent, id} = this.props;
        const newData = {
            ...stateData,
            value: ev.target.value,
            touch: true
        };
        if (parent.fieldChanged)
            parent.fieldChanged(this.props.getDisp(),id,newData,stateData);
        updateRedux({type: 'SETPATH', key: path, value: newData });
    };

    onStateChange = (st) => {
        const {path, updateRedux} = this.props;
        // console.log('onsta',st);
        updateRedux({type: 'SETPATH', key: path, value: st});
    };

    debug (showErrors) {
        //console.log('CONTEXT REND',showErrors,this.props.path);
        return null;
    }

    render () {
        if (this.props.hidden) {
            return null;
        }
        //console.log(this.props);
        const { path, stateData, required, comp, editProps, showLabel = true, validator = defaultValidator, appendo, children } = this.props;
        const value = stateData.value;
        const touch = stateData.touch;
        const id = path.join('-');
        const label = showLabel && translate(id);
        const Compo = comp || InpText;
        const error = true && validator(value, required, id, stateData, editProps);
        const errorText = error && translate(error);
        const errorId = error && (id+'-error');

        // TODO children inside component?
        // Compo.renderChildren(children)

        // console.log('RENDER',path);

        return (
            <ShowErrorsContext.Consumer>
                {showErrors =>
                    <React.Fragment>
                        <div className={'field'} key={id}>
                            {this.debug(showErrors)}
                            {showLabel &&
                            <label className={'field-label'} htmlFor={id}
                                   onClick={this.debug}>{label}{showErrors ? ' E' : ''}</label>
                            }
                            <Compo
                                ariaDescribedBy={errorId}
                                id={id}
                                label={id}
                                onChange={this.onChange}
                                onStateChange={this.onStateChange}
                                required={required}
                                touch={touch}
                                value={value}
                                {...stateData.editProps}
                                {...editProps}
                            />
                            {error &&
                            <span className="field-error" id={errorId}>● {errorText}</span>
                            }
                            {children}
                        </div>
                        {appendo ? appendo(value) : ''}
                    </React.Fragment>
                }
            </ShowErrorsContext.Consumer>
        );
    }

}

function defaultValidator (value, required, id) {
    if (!value && required)
        return id+'-required';
    return null;
}

export function dateValidator (value, required) {
    if (required) {
        if (!value || !value.day || !value.month || !value.year)
            return 'date-required';
    }
    return null;
}

function InpText ({ id, onChange, value, required, maxLength }) {
    return (<input
        type="text" id={id} onChange={onChange} value={value || ''} required={required}
        maxLength={maxLength}
    />);
}

/*
example stateData
    {
    value: '2387288',
    hidden: false,
    touched: true,
    required: false,
    labelId: 'cardRef',
    editProps: {         // passed directly to input component
        maxLength: 11,
        minLength: 4
    }

<Input pattern={alpha} required />
<Input type="tel" required={xxx} />

  PROPS WHICH MATTER TO THE 'FIELD'

    labelId     ie 'idExpiryDate'
    showLabel   (default to true)
    labelValues {}

    id
    path

    value
    touch
    validator   func

  PROPS WHICH MATTER TO THE INPUT

    value
    touch
    optionsId
    required

*/



