import React from 'react';
import PropTypes from 'prop-types';
import Select from './Select';
import {translate} from "../Translator";

const CLS_STD = {
    CLS_COMP: 'date-text',
    CLS_VALID: 'date-text valid',
    CLS_INVALID: 'date-text invalid'
};
const CLS_YEAR = {
    CLS_COMP: 'date-text-year',
    CLS_VALID: 'date-text-year valid',
    CLS_INVALID: 'date-text-year invalid'
};

export default class InputDate extends React.Component {

    static propTypes = {
        ariaDescribedBy: PropTypes.string,
        dateFormat: PropTypes.string,
        id: PropTypes.string,
        invalid: PropTypes.bool,
        onStateChange: PropTypes.func,
        required: PropTypes.bool,
        useMonthSelect: PropTypes.bool,
        value: PropTypes.shape({})
    };

    static defaultProps = {
        dateFormat: 'DMY',
        value: {}
    };

    static defaultValidator = dateValidator;

    componentDidUpdate () {
        if (this.state && this.state.showDays) {
            const c = document.getElementById(this.props.id+'-pick');
            // c.scrollIntoView();
            c.focus();
        }
    }

    getValue () {
        const val = this.props.value;
        return val ? { ...val } : {};
    }

    // event handling

    getFocusId (ev) {
        if (ev.relatedTarget) return ev.relatedTarget.id || '';
        if (document.activeElement) return document.activeElement.id || '';
        return '';
    }

    onBlur = (ev) => {
        const id = this.props.id;
        const oth = this.getFocusId(ev);
        console.log('blur',id,oth);
        if (!(oth===id+'-pick')) {
            this.setState({showDays: 0});
        }
        // only trigger touched if navigating away from the overall component
        if (!this.props.touch){
            if (this.getFocusId(ev).indexOf(id)===0) { // IE11 no startsWith
                return;
            }
            this.props.onStateChange( { touch: true, value: this.props.value } );
        }
    };

    onFocus = (ev) => {
        if (this.props.id===ev.target.id) {
            this.setState({showDays: 1});
        }
    };

    dateFieldChange (value) {
        // console.log('FFFFFF',value);
        this.props.onStateChange( { value, touch: this.props.touch } );
    }

    onYear = (ev) => { this.dateFieldChange( { ...this.props.value, year: ev.target.value } ); };
    onMonth = (ev) => { this.dateFieldChange( { ...this.props.value, month: ev.target.value } ); };
    onDay = (ev) => { this.dateFieldChange( { ...this.props.value, day: ev.target.value } ); };

    // rendering

    renderInput (partId,max,value,onChange,fieldInvalid,placeholder,CLS) {
        const { id, required, invalid, ariaDescribedBy } = this.props;
        const notValid = invalid || fieldInvalid(value);
        const cls = notValid ? CLS.CLS_INVALID : value ? CLS.CLS_VALID : CLS.CLS_COMP;
        const thing = partId ? id+'-'+partId : id;
        return (
            <input
                aria-describedby={ariaDescribedBy}
                aria-invalid={notValid}
                aria-label={thing}
                className={cls}
                id={thing}
                maxLength={max}
                onBlur={this.onBlur}
                onChange={onChange}
                onFocus={this.onFocus}
                placeholder={translate(placeholder)}
                required={required}
                type="tel"
                value={value || ''}
            />
        );
    }

    renderMonthSelect (month) {
        const { id, required, invalid, ariaDescribedBy } = this.props;
        //console.log('RENDMON',month);
        const ariaLabel = id+'-month';
        return (
            <div style={{ display: 'inline-block', marginRight: '8px' }}>
                <Select
                    ariaDescribedBy={ariaDescribedBy}
                    ariaLabel={ariaLabel}
                    className="date-select"
                    id={id+'-month'}
                    invalid={invalid}
                    options="dateMonths"
                    onBlur={this.onBlur}
                    onChange={this.onMonth}
                    placeholder="MM"
                    required={required}
                    value={month}
                />
            </div>
        );
    }

    renderByCode (code,value) {
        switch(code) {
            case 'D': return this.renderInput(null,2,value.day,this.onDay,isInvalidDay,'DD',CLS_STD); // was 'day'
            case 'M':
                if (this.props.useMonthSelect) {
                    return this.renderMonthSelect(value.month);
                }
                return this.renderInput('month',2,value.month,this.onMonth,isInvalidMonth,'MM',CLS_STD);
            case 'Y': return this.renderInput('year',4,value.year,this.onYear,isInvalidYear,'YYYY',CLS_YEAR);
            default: return null;
        }
    }

    clickDay = (ev) => {
        console.log('clickDay', ev.target, ev.target.nodeName, ev.target.innerText);
        if (ev.target.nodeName==='TD') {
            this.setState({showDays: 0});
            this.dateFieldChange( { ...this.props.value, day: ev.target.innerText } );
            const c = document.getElementById(this.props.id+'-month');
            console.log(c);
            c.focus();
        }
    };

    render () {
        const { value, dateFormat, id } = this.props;
        const val = value || {};
        const showDays = this.state && this.state.showDays;
        return (
            <span>
                {this.renderByCode(dateFormat.charAt(0),val)}
                {this.renderByCode(dateFormat.charAt(1),val)}
                {this.renderByCode(dateFormat.charAt(2),val)}
                <br/>
                <table className={showDays ? "date-pick-day" : 'date-pick-day-hidden'} onBlur={this.onBlur} onClick={this.clickDay} tabIndex="0" id={id+'-pick'}>
                    <tbody>
                    <tr><td>1</td><td>2</td><td>3</td><td>4</td><td>5</td><td>6</td><td>7</td></tr>
                    <tr><td>8</td><td>9</td><td>10</td><td>11</td><td>12</td><td>13</td><td>14</td></tr>
                    <tr><td onClick={this.clickDay}>15</td><td>16</td><td>17</td><td>18</td><td>19</td><td>20</td><td>21</td></tr>
                    <tr><td>22</td><td>23</td><td>24</td><td>25</td><td>26</td><td>27</td><td>28</td></tr>
                    <tr><td>29</td><td>30</td><td>31</td></tr>
                    </tbody>
                </table>
            </span>
        );
    }
}

/*  Short labels for accessible controls
    Expiry date - month
    Date of birth - day
    Purchase date - year

    Year - invalid
 */

function dateValidator ( val, props ) {
    if (val) {
        if (isInvalidDay(val.day)) {
            return 'invalidDay';
        }
        if (isInvalidMonth(val.month)) {
            return 'invalidMonth';
        }
        if (isInvalidYear(val.year)) {
            return 'invalidYear';
        }
    }
    if (!val || !val.year || !val.month || !val.day) {
        if (props.required)
            return 'isRequired';
    }
}

function isInvalidDay (day) {
    const d = parseInt(day,10);
    return day && !(d>=1 && d<=31); // TODO check with month and year
}

function isInvalidMonth (mon) {
    const m = parseInt(mon,10);
    return mon && !(m>=1 && m<=12);
}

function isInvalidYear (year) {
    const y = parseInt(year,10);
    return year && !(y>=1 && y<=2100); // TODO use prop limits, with touch state
}



