import React, {Component} from 'react';
import PropTypes from 'prop-types';
import ConnectedElement from './ConnectedElement';

export default class ChildElement extends Component {

    static propTypes = {
        id: PropTypes.string,
        ChildComponent: PropTypes.func
    };

    static contextTypes = {
        getSection: PropTypes.func,
        getSectionId: PropTypes.func
    };

    render () {
        const { id, ChildComponent } = this.props;
        const elemId = this.context.getSectionId() + '-' + id;
        const sect = this.context.getSection();

        return <ConnectedElement id={elemId} InputComponent={ChildComponent} section={sect}/>;
    }

}

export function makeChildElement (id,ChildComponent) {
    /*return function () {
        return <ChildElement id={id} ChildComponent={ChildComponent}/>;
    }*/
    class SectionChild extends ChildElement {
        static defaultProps = {
            id,
            ChildComponent
        };
        static id = id;
    }
    return SectionChild;
}

/*
note ChildComponent is usually a proper field, containing an input or select
*/



