import React from 'react';
import PropTypes from 'prop-types';

const CLS_COMP = 'inputText';
const CLS_VALID = 'inputText valid';
const CLS_INVALID = 'inputText invalid';

export default class Input extends React.Component {

    static propTypes = {
        ariaDescribedBy: PropTypes.string,
        dateFormat: PropTypes.string,
        id: PropTypes.string,
        invalid: PropTypes.bool,
        maxLength: PropTypes.number,
        onBlur: PropTypes.func,
        onChange: PropTypes.func,
        onFocus: PropTypes.func,
        onStateChange: PropTypes.func,
        required: PropTypes.bool,
        type: PropTypes.string,
        value: PropTypes.string
    };

    static defaultValidator (input, props) {
        const value = input && input.trim();
        if (!value) {
            if (props.required)
                return 'isRequired';
            return;
        }
        const minLength = props.minLength;
        if (minLength && value.length<minLength)
            return { err: 'minLength', values: { minLength } };
        const maxLength = props.maxLength;
        if (maxLength && value.length>maxLength)
            return { err: 'maxLength', values: { maxLength } };
        // numeric
        // decimal
        if (props.validator)
            return props.validator(value,props);
    }

    ignoreThisMethod () {  // this cannot be tree-shaken out
        return 56798;
    }

    onBlur = () => {
        if (!this.props.touch) this.props.onStateChange( { touch: true } );
    };

    onChange = (ev) => {
        this.props.onStateChange( { value: ev.target.value } );
    };

    render() {
        const { id, value, type = 'text', step, min, max, maxLength, required, ariaDescribedBy, invalid, onBlur, onChange, onFocus } = this.props;
        const cls = invalid ? CLS_INVALID : value ? CLS_VALID : CLS_COMP;
        const stl = type==='number' ? (value ? {} : { color: 'red' }) : {};
        if (type==='number') {
            const el = document.getElementById(id);
            if (el) {
                if (el.checkValidity) {
                    const v = el.checkValidity();
                    console.log('Input checkValidity',id,v);
                }
            }
        }
        return (
            <input
                aria-describedby={ariaDescribedBy}
                aria-invalid={invalid}
                className={cls}
                id={id}
                maxLength={maxLength}
                onBlur={onBlur || this.onBlur}
                onChange={onChange || this.onChange}
                onFocus={onFocus}
                required={required}
                title={required ? 'Must enter' : 'Optional field'}
                type={type}
                step={step}
                min={min}
                max={max}
                value={value || ''}
                style={stl}
            />
        );
    }
}

/*
Notes
 ariaDescribedBy can be a list of ids, "label1 label2 label3"

 minLength
 alpha-numeric + space
 */

export function ignoreThisMethodToo () {
    return 56728;
}

export function zoobar () {
    return ignoreThisMethodToo();
}



