export function makeChildField (parent, defaultProps) {

    const idpath = parent.getId() + PATH_SEP + defaultProps.id;
    const path = idpath.split(PATH_SEP);

    function mapStateToProps (state) {
        const stateData = lookupValue(path,state.formState,null);
        return {
            ...defaultProps,
            idpath,
            compo: defaultProps.component,
            stateData,
            coreData: state.formState.coreData
        };
    }

    const c = connect(mapStateToProps,{updateRedux})(FieldRender);
    c.defaultValidator = (val,prp) => {
        return defaultProps.component.defaultValidator( val, {...defaultProps,...prp} );
    };
    return c;
}

export function makeNoboField (id,component,defaultProps) {

    function mapStateToProps (state,props) {
        const path = props.idpath.split(PATH_SEP);
        const stateData = lookupValue(path,state.formState,null);
        return {
            ...defaultProps,    // ??? expensive
            id,
            compo: component,
            stateData, // : state.formState[path] || DEF_DATA
            coreData: state.formState.coreData
        };
    }

    const Thing = connect(mapStateToProps,{updateRedux})(FieldRender);

    class FieldContext extends React.PureComponent {

        // static displayName = 'F_'+id;

        /*shouldComponentUpdate (nextProps) {
            console.log('should',id,this.props,nextProps);
            return false;
        }*/

        render () {
            // console.log('Nobo render',id);
            const props = this.props;
            return (
                <FormSectionContext.Consumer>
                    {section =>
                        <Thing idpath={ section.getId() + PATH_SEP + id } {...props} />
                    }
                </FormSectionContext.Consumer>
            );
        }
    }

    FieldContext.displayName = 'F_'+id;
    FieldContext.defaultValidator = (val,prp) => {
        return component.defaultValidator( val, {...defaultProps,...prp} );
    };

    return FieldContext;
}

// common redux setter

export const updateRedux = (action) => {
    return function (dispatch/*, getState*/) {
        return dispatch(action);
    };
};

// simplest component

function mapStateToProps (state,props) {
    // console.log('MSPOSPPS',state);
    const idpath = props.sectionId + PATH_SEP + props.id;
    const path = idpath.split(PATH_SEP);
    const stateData = lookupValue(path,state.formState,null);
    return {
        idpath,
        stateData,
        coreData: state.formState.coreData
    };
}

// the react component that does the business
// renders a label, input field, and inline error

class FieldRender extends React.PureComponent {

    onBlur = (ev) => {
        if (this.props.touched) {
            return;
        }
        // set touched state
        if (this.props.onBlur) {
            this.props.onBlur(ev);
        }
    };

    onChange = (ev) => {
        const { idpath, updateRedux } = this.props;
        //console.log('onCHAGDS',idpath);
        // updateRedux({type: 'SET', key: idpath, value: ev.target.value });
        const path = idpath.split(PATH_SEP);

        updateRedux({type: 'SETPATH', key: path, value: ev.target.value });
        //console.log('RESP',q);

        if (this.props.onChange)
            this.props.onChange(ev);
    };

    onStateChange = (data) => {
        const { idpath, updateRedux } = this.props;
        //console.log('onSTATS',idpath);
        if (data.value!==undefined) {
            // updateRedux({type: 'SET', key: idpath, value: data.value});
            const path = idpath.split(PATH_SEP);
            updateRedux({type: 'SETPATH', key: path, value: data.value});
        }
    };

    render () {
        const { id, idpath, compo, stateData, foo, noLabel = false, coreData, touched,
            onChange,  // filter these out - do not pass to component !
            ...others } = this.props;  // eslint-disable-line no-unused-vars
        const Compo = compo;
        const label = translate(idpath);

        // error handling
        let error,errorId,errorText;
        if (touched || coreData.showAllErrors) {
            if (this.props.myValidator) {
                const err = this.props.myValidator(stateData,this.props,idpath);
                console.log('ERRS',err);
                if (err && err.error) {
                    error = err;
                    errorText = err.error;
                }
            } else {
                error = Compo.defaultValidator && Compo.defaultValidator(stateData,this.props);
                if (error) {
                    error = error.err || error;
                    errorText = translate(idpath + PATH_SEP + error);
                    errorId = idpath + PATH_SEP + 'error';
                }
            }
        }

        return (
            <div className={'field'} key={id}>
                {!noLabel &&
                    <label className={'field-label'} htmlFor={idpath}>{label}{foo ? foo : ''}</label>
                }
                <Compo
                    ariaDescribedBy={errorId}
                    id={idpath}
                    invalid={!!error}
                    onChange={this.onChange}
                    onStateChange={this.onStateChange}
                    placeholder={id}
                    touched={touched}
                    value={stateData}
                    {...others}
                />
                {error &&
                    <span className="field-error" id={errorId}>● {errorText}</span>
                }
            </div>
        );
    }
}

// export FieldRender connected to redux

export const FormFieldRender = connect(mapStateToProps,{updateRedux})(FieldRender);



