let curr = 'GB';
let copyData = copyData_EN;
let copyCache = {};

export function translate (id) {
    return copyCache[id] || (copyCache[id] = digDown(id,id));
}

export function translateExact (id) {
    return copyData[id];
}

export function translateWithValues (id, values) {
    let temp = copyCache[id] || (copyCache[id] = digDown(id,id));
    Object.keys(values).forEach( key => {
        const rep = values[key];
        temp = temp.replace( '{'+key+'}', rep );
    });
    return temp;
}

function digDown (base,id) {
    //console.log('digdown',id);
    const index = id.indexOf('-');
    if (index>0) {
        const sub = id.substring(index+1);
        return copyData[sub] || digDown(base,sub);
    }
    return copyData[base] || base;
}

export function getOptionSet (name) {
    const set = copyData.options[name];
    if (set) return set;
    const keyList = copyData.optionKeys[name] || [];
    const newSet = {};
    keyList.forEach( key => { newSet[key] = key; });
    return copyData.options[name] = newSet;
}

export function getOptionList (name) {
    const done = copyData.optionLists[name];
    if (done) return done;
    const keySet = copyData.options[name] || {};
    const keyList = copyData.optionKeys[name] || [];
    if (!keyList.length) {
        // make a list sorted by text from the set
    }
    const list = [];
    keyList.forEach( key => {
        const text = keySet[key] || key;
        list.push({ id: key, text });
    });
    copyData.optionLists[name] = list;
    return list;
}

export function getLanguage () {
    return curr;
}

export function setLanguage (ev) {
    const c = ev.target ? ev.target.value : ev;
    curr = c;
    const applyStyle = copyData.applyStyle;
    const map = {
        [c]: copyData,
        CN: copyData_CN,
        US: copyData_US,
        GB: copyData_EN,
        RU: copyData_RU,
        IL: copyData_HE,
        AR: copyData_AR
    };
    copyData = map[c];
    // reset caches
    copyData.optionLists = {};
    copyCache = {};
    // css for direction
    if (copyData.documentRTL)
        document.body.classList.add('forms-rtl');
    else
        document.body.classList.remove('forms-rtl');
    // css array
    if (applyStyle)
        document.body.classList.remove(...applyStyle);
    if (copyData.applyStyle)
        document.body.classList.add(...copyData.applyStyle);
    // listeners
    listeners.forEach( L => L.setState({ lang: c }) )
}

const listeners = [];

export function addListener (L) {
    listeners.push( L );
}

