import React from 'react';
import {translate, getOptionList} from "../Translator";

export default function Radios ({ ariaDescribedBy, id, value, options, label, onStateChange }) {

    function onChange (ev) {
        // console.log('RA CHANGE',ev.target.value, value);
        onStateChange( { value: ev.target.value } );
    }

    /*function onClick (ev) {   // breaks ie 11 ?
        console.log('RA CLICK',ev.target.value,value);
        if (value===ev.target.value)
            onStateChange( { value: '' } );
    }*/

    function renderRadios () {
        const list = getOptionList(options);
        const opts = [];
        list.forEach( row => {
            const key = row.id;
            const full = id+'-'+key;
            opts.push(
                <span key={full}>
                    <input
                        aria-describedby={ariaDescribedBy}
                        id={full} name={id} type="radio" value={key} checked={key===value}
                        onChange={onChange}
                    />
                    <label htmlFor={full}>
                        {row.text}
                    </label>
                </span>
            );
        });
        return opts;
    }

    const labelText = translate(label);
    return (
        <fieldset aria-label={labelText} className="radio-fieldset" id={id}>
            <legend>{labelText}</legend>
            {renderRadios()}
        </fieldset>
    );
}



