import React, {Component} from 'react';
import logo from './logo.svg';
import './App.css';
import SelectCountry from './SelectCountry';
import { makeMade } from './ConnectedElement';
// import BippoForm from './hack/BippoForm';
import PageLink from "./hack/PageLink";
import BippoField from "./hack/BippoField";
import makeField from "./hack/RedoConnecto";
import Select from './components/Select';       // eslint-disable-line no-unused-vars
import RadiosFunc from './components/RadiosFunc';
import Input from './components/Input';
import UltraSection from './hack/UltraSection';
import {translate,setLanguage} from "./Translator";
import NoboTest from './nobo/NoboTest';
import { FormFieldRender } from "./nobo/NoboField";
import ReadItOut from "./components/ReadItOut";

export default class ThirdPage extends Component {

    componentDidMount () {
        console.log('componentDidMount ThirdPage');
    }

    LangSelect = makeMade('nationality', SelectCountry, this );

    back = () => {
        window.history.go(-1);
    };

    backTop = () => {
        window.scrollTo(0,0);
        window.history.go(-1);
    };

    render() {
        const Lang = this.LangSelect;
        return (
            <div className="App">
                <header className="App-header">
                    <img src={logo} className="App-logo" alt="logo"/>
                    <h1 className="App-title">Third Page</h1>
                    <ReadItOut>This is page 3</ReadItOut>
                </header>

                <div className="form">
                    <NoboTest id="NT1"/>
                    <NoboTest id="NT2"/>
                </div>

                <UltraSection showErrors={false}>
                <div className="form">
                    <div style={{ maxWidth: '300px', marginTop: '32px' }}>
                        <Lang/>
                        <PageLink url="/zooter" ttl="Home page"/>
                        <PageLink url="/zooter/2" ttl="Second Page"/>
                        <PageLink url="/zooter/4" ttl="4th Page"/>
                    </div>

                    <h2>{translate('Employment Details')}</h2>

                    <DupForm id="MagicDupForm1" path={['employmentDetails']}/>
                    <DupForm id="MagicDupForm2" path={['employmentDetails','DF2']}/>

                    <h2>{translate('Income Details')}</h2>

                    <FormFieldRender id={'lang'} sectionId={'coreData'} compo={Select} options="country" onChange={setLanguage}/>

                    <div style={{ textAlign: 'right', marginTop: '18px' }}>
                        <Button text="back/top" onClick={this.backTop}/>
                        {' '}
                        <button className="btn btn-default" onClick={this.back}>{translate('back')}</button>
                        {' '}
                        <PageLink ttl="continue" type="primary" url="/zooter/4" />
                    </div>
                    <p>...</p>
                </div>
                </UltraSection>

            </div>
        );
    }
}

function Button ({ text, onClick, type = 'default' }) {
    return <button className={"btn btn-"+type} onClick={onClick}>{translate(text)}</button>;
}

function makeText (t,id,props) { return makeField(t,BippoField,id,props); }

class DupForm extends React.Component {

    doEmpType = (value) => {
        //console.log('doEmpType',value);
        if (value==='UN') {
            return <this.Other/>;
        }
        return null;
    };

    EmpStatus = makeText(this, 'employmentStatus', { required: true, comp: RadiosFunc, editProps: { options: 'employment' }, appendo: this.doEmpType, showLabel: false } );
    EmpCat = makeText(this, 'employmentCategory', { required: true, comp: Input } );
    EmpType = makeText(this, 'employmentType', { appendo: this.doEmpType });
    Other = makeText(this, 'otherEmploymentType', { required: true });

    render () {
        const {EmpStatus,EmpCat,EmpType} = this;
        return (
            <form>
                <EmpStatus/>
                <EmpCat/>
                <EmpType/>
            </form>
        );
    }
}

