import React from 'react';
import PropTypes from 'prop-types';

/* base class for things like AddressSection */

export default class FormElement extends React.Component {

    static propTypes = {
        id: PropTypes.string    // 'myForm-homeAddress'
    };

    static childContextTypes = {
        getSection: PropTypes.func,
        getSectionId: PropTypes.func
    };

    getChildContext () {
        return {
            getSection: () => { return this; },
            getSectionId: () => { return this.props.id; }
        };
    }

    // this is called from ConnectedElement when a field value is changed

    somethingChanged (dispatch,id,data,oldData) {       // id example 'myForm-homeAddress-country'
        console.log('somethingChanged',id,data);

        if (id.endsWith('-country')) {
            const value = data.value==='OTH';
            const key = this.props.id + '-otherCountry';
            dispatch({ type: 'SHOW', key, value });
        }

        if (id.endsWith('-address1')) {
            if (data.value==='8') {
                const key = this.props.id + '-city';
                dispatch({ type: 'SET', key, value: { labelId: 'Enter a city!' } });
            } else if (oldData.value==='8') {
                const key = this.props.id + '-city';
                dispatch({ type: 'SET', key, value: { touched: true } });
            }
        }
    }

}



