import React from 'react';
// import {translate} from "../Translator";

export default function PageLink ({ id, url, ttl, type }) {

    function doLink () {
        window.scrollTo(0,0);
        window.history.pushState(null,ttl,url);

        try {
            console.log('POPSTATEEV',typeof window.PopStateEvent);
            const popStateEvent = new PopStateEvent('popstate', {state: {}});
            dispatchEvent(popStateEvent);
        } catch (err) {
            console.log('POPSTATEFAIL',err);
            const ev = document.createEvent("Event");
            //let doesnt_bubble = false;
            //let isnt_cancelable = false;
            //ev.initEvent("popstate", doesnt_bubble, isnt_cancelable);
            ev.initEvent("popstate", false, false);
            dispatchEvent(ev);
        }
    }

    return (
        <Button onClick={doLink} type={type}>{ttl}</Button>
    );
}

function Button ({ children, onClick, type = 'default' }) {
    return <button className={"btn btn-"+type} onClick={onClick}>{children}</button>;
}



