function getOptions (name) {
    const list = getOptionList(name);
    const opts = [];
    list.forEach( row => {
        opts.push( <option key={row.id} value={row.id}>{row.text}</option> );
    });
    return opts;
}

function checkValue(name,val) {
    const set = getOptionSet(name);
    return !!set[val] ? val : '';
}

export default class SelectCountry extends Component {

    static validateInput (props) {
        const rendervalue = checkValue('country',props.value);
        if (!rendervalue) {
            return props.id + '-error';
        }
    }

    render () {
        const { id, onBlur, onChange, value = '', optional } = this.props;
        const rendervalue = checkValue('country',value);
        const cnm = rendervalue!=='' ? 'input' : 'input empty';
        const please = optional || !rendervalue;
        const pleaseSelect = translate(id+'-please-select');
        return (
            <div className="select-container">
            <select className={cnm} id={id} onBlur={onBlur} onChange={onChange} value={rendervalue}>
                {please && <option disabled={!optional} value="">{pleaseSelect}</option>}
                {getOptions('country')}
            </select>
            </div>
        );
    }

}



