import React from 'react';
import FormElement from './FormElement';
import { translate } from './Translator';
// needed for make
// import ChildElement from './ChildElement';
import InputText from './InputText';
import SelectCountry from './SelectCountry';
import { makeProperField } from './ProperField';
import { makeChildElement } from './ChildElement';

const Address1 = makeTextInput('address1');
const City = makeTextInput('city', { optional: true } );
const Postcode = makeTextInput('postcode',{ maxLength: 10, minLength: 4 });
const Country = makeSelect('country', { optional: false });
const OtherCountry = makeTextInput('otherCountry', {}, { show: false });

export default class AddressSection extends FormElement {

    static getStateFields (/* clues */) {  // for validation and sanitization
        return [
            Address1,
            City,
            Postcode,
            Country,
            OtherCountry
        ];
    }

    static makeAddress (id) {
        /*return function () { return <AddressSection id={id} />; }*/
        class AddressImpl extends AddressSection {
            static defaultProps = { id };
        }
        return AddressImpl;
    }

    debug = () => {
        const sf = AddressSection.getStateFields();
        console.log(sf);
        sf.forEach( (item,index) => {
            console.log(index,item.id);
        });
        this.setState({ heppy: Date.now() });
    };

    render () {
        return (
            <div>
                <p onClick={this.debug}>{translate(this.props.id)}</p>
                <Address1/>
                <City/>
                <Postcode/>
                <Country/>
                <OtherCountry/>
            </div>
        );
    }
}

// move elsewhere

function makeTextInput (id, editProps, showProps) {
    const thingy = makeProperField(InputText,editProps,showProps);
    /*return function () {
        return <ChildElement id={id} Thing={thingy}/>;
    }*/
    return makeChildElement(id,thingy);
}

function makeSelect (id,editProps) {
    const thingy = makeProperField(SelectCountry,editProps);
    return makeChildElement(id,thingy);
}



