import React from 'react';

export default function ReadItOut ({ children }) {

    const key = 'RE'+String(Date.now());

    return (
        <span
            aria-atomic="true"
            aria-live="polite"
            className="sr-only"
            id={key}
            key={key}
            role="status"
        >
            {children}
        </span>
    );
}

/*
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
 */



