import {combineReducers, createStore, applyMiddleware} from 'redux';
import thunk from 'redux-thunk';

const initialData = window.initialData || {};

const DEF_STATE = {
    coreData: {
        lang: 'en',
        showAllErrors: false
    },
    myTestField: { value: 'something '+initialData.foobar },
    totalAmount: { value: '3877' },
    personalDetails: {
        employment: {
            jobTitle: { value: 'Pie Tester', touched: true },
            salary: { value: '480000' }
        },
        addressHistory: [
            { address: 'Bogfield', postcode: 'S9 2DE', region: { value: 'NC', optionId: 'us_states' } },
            { address: '11 Pig St', postcode: 'DS7 1DP' },
        ]
    }
};

const specialHandlers = {};

export function setHandler( handlerKey, func ) {
    specialHandlers[handlerKey] = func;
}

const DEF_OBJECT = Object.seal( {} );

export const lookupNamed = (state, path, name, defaultResult = DEF_OBJECT) => {
    const found = path.reduce((thing, x) => thing[x] || DEF_OBJECT, state)[name];
    return found!==undefined ? found : defaultResult;
};

export const lookupValue = (path, state) => {
    const val = path.reduce((thing, x) => (thing[x]!==undefined) ? thing[x] : DEF_OBJECT, state);
    return val === DEF_OBJECT ? undefined : val;
};

export const lookup = (path, state, defaultResult = DEF_OBJECT) =>
    path.reduce((thing, x) => (thing[x]!==undefined) ? thing[x] : defaultResult, state);

function lookup2(path, state, defaultResult = DEF_OBJECT, index = 0) {  // eslint-disable-line
    const thing = state[path[index++]];
    if (thing===undefined) { // what if thing is zero or false ?
        return defaultResult;
    }
    if (index>=path.length) {
        return thing;
    }
    return lookup2(path, thing, defaultResult, index);
}

function change (state, name, value, handlerKey) {
    if (Array.isArray(state)) {
        state = [...state];
        state[name] = value;
        return state;
    }
    if (handlerKey) {
        return specialHandlers[handlerKey](name, value, { ...state });
    }
    return { ...state, [name]: value };
}

function setThing( path, value, state = DEF_STATE, handlerKey, index = 0 ) {
    const name = path[index++];
    if (index>=path.length) {
        return change(state, name, value, handlerKey);
    }
    let thing = state[name];
    if (!thing) { // it doesn't exist yet
        thing = isNaN(path[index]) ? {} : []; // if the next getter is numeric, then this is an array
    }
    const modi = setThing(path,value,thing,handlerKey,index);
    return change(state, name, modi);
}

function dbgThing(path,value,state,handlerKey) {       // eslint-disable-line
    const res = setThing(path,value,state,handlerKey);
    console.log(JSON.stringify(res,null,' '));
    return res;
}

export function moveInArray (inArray, from, to) {
    const len = inArray.length;
    if (!len || from>=len || from<-len) // prevent undefined from being inserted in array
        return inArray;
    const arr = [...inArray];
    arr.splice(to, 0, arr.splice(from, 1)[0]);
    return arr;
}

// lookup(['personalDetails','employment','jobTitle']);
// lookup(['personalDetails','addressHistory',1,'postcode']);
// dbgThing( ['personalDetails','employment','jobTitle'], { value: 'Infestor' } );
// dbgThing( ['personalDetails','addressHistory',2,'city'], { value: 'Sheffield' } );
// dbgThing( ['personalDetails','addressHistory',1,'city'], { value: 'Doncaster' } );
// dbgThing( ['personalDetails','quotes',0,'applicant',0,'quoteDate'], { value: '20180211' } );
// dbgThing(['XXX'],100);

// reducer

function formState(state = DEF_STATE, action) {
    // console.log('REDX',action);
    if (action.type === 'SET') {
        return {
            ...state,
            [action.key]: action.value
        };
    }
    if (action.type === 'SETPATH') {
        return setThing(action.key,action.value,state,action.handlerKey);
    }
    if (action.type === 'SHOW') {
        const field = state[action.key] || {};
        if (!!field.show === !!action.value)
            return state;
        return {
            ...state,
            [action.key]: { ...field, show: action.value }
        };
    }
    if (action.type === 'BOOST') {
        const mod = { ...state };
        Object.keys(state).forEach( key => {
            // console.log('KEY',key);
            const thing = state[key];
            if (typeof thing === 'object')
                mod[key] = { ...thing, ticktick: Date.now() };
        });
        mod.lang = action.value;
        return mod;
    }
    return state;
}

const rootReducer = combineReducers({
    formState
});

function configureStore() {
    return createStore(
        rootReducer,
        window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__(),
        applyMiddleware(thunk)
    );
}

const store = configureStore();

export default store;



