import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';

class ConnectedElement extends Component {

    static propTypes = {
        InputComponent: PropTypes.func,
        id: PropTypes.string,
        stateData: PropTypes.shape({}),     // comes from redux, state.formState[props.id]
        section: PropTypes.shape({})        // the parent of this
    };

    // shouldComponentUpdate () {}

    onBlur = () => {
        const {id, stateData} = this.props;
        if (stateData.touched)
            return;
        const newData = {
            ...stateData,
            touched: true
        };
        this.props.updateRedux({type: 'SET', key: id, value: newData});
    };

    onFocus = () => {
        this.blurt = setTimeout( this.onBlur, 2000);   // error handling without exit field
    };

    onChange = (ev) => {
        clearTimeout(this.blurt);
        this.blurt = setTimeout( this.onBlur, 1000);   // error handling without exit field

        // console.log('CE onchange',this.props.id, ev.target.value);
        const {id, stateData} = this.props;
        const newData = {
            ...stateData,
            value: ev.target.value
        };
        if (this.props.section && this.props.section.somethingChanged)
            this.props.section.somethingChanged(this.props.getDisp(),id,newData,stateData);
        this.props.updateRedux({type: 'SET', key: id, value: newData});
    };

    render () {
        const {id, stateData, InputComponent} = this.props;

        // onChange - onBlur - show
        return (
            <InputComponent
                {...stateData}
                id={id}
                onBlur={this.onBlur}
                onChange={this.onChange}
                onFocus={this.onFocus}
            />
        );
    }
}

let DEF_DATA = {};

function mapStateToProps (state, props) {
    // console.log('MSP', props);
    return {
        stateData: state.formState[props.id] || DEF_DATA
    };
}

const updateRedux = (action) => {
    return function (dispatch/*, getState*/) {
        return dispatch(action);
    };
};

const getDisp = () => {
    return function (dispatch/*, getState*/) {
        return dispatch;
    };
};

export const mapDispatchToProps = {
    updateRedux, getDisp
};

export default connect(mapStateToProps,mapDispatchToProps)(ConnectedElement);

/*export function makeMadeX (id, comp, theSection) { // FAIL
    const Elem = connect(mapStateToProps, mapDispatchToProps)(ConnectedElement);
    return <Elem id={id} InputComponent={comp} section={theSection}/>;
}*/

export function makeMade1 (id, comp, theSection) {
    return function RenderElement () {
        const Elem = connect(mapStateToProps, mapDispatchToProps)(ConnectedElement);
        return <Elem id={id} InputComponent={comp} section={theSection}/>;
    };
}

export function makeMade (id, comp, theSection) {
    const Elem = connect(mapStateToProps, mapDispatchToProps)(ConnectedElement);
    class RenderComp extends Component {
        render () {
            return <Elem id={id} InputComponent={comp} section={theSection}/>;
        }
    }
    return RenderComp;
}

export function redrawDefaults () {
    DEF_DATA = {};
}



