// import React from 'react';
import {connect} from 'react-redux';
import {lookup} from '../startRedux';

const updateRedux = (action) => {
    return function (dispatch/*, getState*/) {
        return dispatch(action);
    };
};

const getDisp = () => {
    return function (dispatch/*, getState*/) {
        return dispatch;
    };
};

const getRedux = () => {
    return function (dispatch, getState) {
        return getState();
    };
};

const mapDispatchToProps = {
    updateRedux, getDisp, getRedux
};

export default function makeConnected ( parent, component, id, defaultProps ) {

    const path = parent ? [ ...parent.props.path, id ] : [ id ];

    function mapStateToProps (state /*,props*/) {
        const stateData = lookup(path,state.formState,DEF_DATA);
        //console.log('MSP',id,path,stateData);
        return {
            ...defaultProps,
            id,
            parent: parent,
            path: path,
            stateData, // : state.formState[path] || DEF_DATA
            lang: state.formState.lang  // TODO this could be globalData
        };
    }

    const thing = connect(mapStateToProps,mapDispatchToProps)(component);

    thing.id = id;

    thing.validator = () => {
        console.log('THING VALIDAE',thing.props);
    };

    thing.getValue = () => {
        const it = parent.props.stateData[id]; // if a section, then 'it' is the value
        return it && it.value;
    };

    return thing;
}

const DEF_DATA = {};

/*  alternate method
    <AnotherField id="anotherField" parent={this} required />
*/

function standardMapStateToProps (state, { id, parent } ) {
    const path = parent ? [ ...parent.props.path, id ] : [ id ];

    const stateData = lookup( path, state.formState,DEF_DATA );
    // console.log('MSP',path,stateData);
    return {
        // parent: parent,
        path: path,
        stateData,
        lang: state.formState.lang  // TODO this could be globalData
    };
}

export function connectByParent (component) {       // eslint-disable-line no-unused-vars
    return connect(standardMapStateToProps,mapDispatchToProps)(component);
}

// SURELY THE EASIEST METHOD...

function pathMapStateToProps (state, { path } ) {
    const stateData = lookup( path, state.formState, DEF_DATA );
    return {
        stateData,
        lang: state.formState.lang  // TODO this could be globalData
    };
}

export function connectByPath (component) {       // eslint-disable-line no-unused-vars
    return connect(pathMapStateToProps,mapDispatchToProps)(component);
}



