import React, {Component} from 'react';
import PropTypes from 'prop-types';
import { translate } from './Translator';

class ProperField extends Component {

    static propTypes = {
        id: PropTypes.string,               // 'myForm-homeAddress-postcode'
        InputComponent: PropTypes.func,     // text or select input
        labelId: PropTypes.string,
        onBlur: PropTypes.func,
        onChange: PropTypes.func,
        editProps: PropTypes.shape({}),     // props for the input component
        show: PropTypes.bool,
        touched: PropTypes.bool,
        value: PropTypes.oneOfType( [PropTypes.shape({}), PropTypes.string] )
    };

    render () {
        const {id, touched, value = '', labelId, editProps = {}, onBlur, onChange, onFocus, InputComponent, show } = this.props;
        if (show===false) {
            return null;
        }
        const label = translate(labelId || id);

        const error = touched && InputComponent.validateInput && InputComponent.validateInput(this.props);
        const invalid = !!error;

        //const invalid = touched && !value && editProps.optional!==true; // here, validator instead of !value
        //const error = invalid ? translate(id+'-error') : '';

        return (
            <div className={invalid ? 'field invalid' : 'field'}>
                <label htmlFor={id}>{label}</label>
                <InputComponent {...editProps} type="text" id={id} onBlur={onBlur} onChange={onChange} onFocus={onFocus} value={value}/>
                {invalid && <span id={error}>● {translate(error)}</span>}
            </div>
        );
    }

}

export function makeProperField (comp, editProps, showProps) {
    class Field extends ProperField {
        static defaultProps = {
            InputComponent: comp,
            editProps,
            ...showProps
        };
    }
    return Field;
}



