import React from 'react';
import HomePage from './HomePage';
import SecondPage from './SecondPage';
import ThirdPage from './ThirdPage';
//import FourthPage from './nobo/FourthPage';
import { addListener } from './Translator';

export default class App extends React.Component {

    componentDidMount () {
        window.addEventListener('popstate', (ev) => {
            console.log('POPSTATE',ev);
            this.setState( { dt: Date.now() } );
        });
        // lang change
        addListener(this);
    }

    loadPage5 (loaded, failed) {
        // import ('./nobo/FifthPage').then(loaded).catch(failed);
    }

    loadPage4 (loaded, failed) {
        import ('./nobo/FourthPage' /* webpackChunkName: "PAGE4" */ ).then(loaded).catch(failed);
    }

    loadAPage (name, loader, title) {
        const Thing = this.name;
        if (Thing) {
            document.title = title;
            return <Thing />;
        }
        loader(
            (module) => {
                this.name = module.default;
                this.setState( { dt: Date.now() } );
            },
            (e) => {
                console.log('PAGE LOAD FAILED!',e);
            }
        );
        return <div>Page is loading...</div>;
    }
/*
    loadPage4aa () {
        const app = this;
        const { FourthPage } = app;
        if (FourthPage) {
            return <FourthPage/>;
        }
        import ('./nobo/FourthPage')
            .then( (module) => {
                //console.log('LOADED',FourthPage);
                //console.log('EXAMPLE',ThirdPage);
                //console.log('WOTWEG',FourthPage.default);
                app.FourthPage = module.default;
                // app.FourthPage = <div>Page loaded!</div>;
                app.setState( { dt: Date.now() } );
            })
            .catch( e => {
                console.log('LOAD FAILED',e);
            }) ;
        return <div>Page loading...</div>;
    }
*/
    render() {
        const loca = window.location;
        // console.log(loca);

        switch(loca.pathname) {
            case '/zooter/2':
            case '/2':
                document.title = 'Enter your personal details';
                return <SecondPage/>;
            case '/zooter/3':
            case '/3':
                document.title = 'Describe your favorite food!';
                return <ThirdPage/>;
            case '/zooter/4':
            case '/4':
                return this.loadAPage('Page4',this.loadPage4,'This is page 4 of this application');
                // return  this.loadPage4aa('./nobo/FourthPage'); //<FourthPage/>;
            default:
                document.title = 'You are on the home page';
                return <HomePage/>;
        }

    }
}

